@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
:root{
	--backgroundGlobal:				#872D62;
	--backgroundDarkerGlobal: 		#651142;
	--backgroundLigherGlobal: 		#A95486;

	--backgroundDisabledGlobal:		#474747;
	
	--colorGlobal:					#CB87AF;
	--backgroundCorrect: 			#4dc46b;
}

*{
	font-family: 'Noto Sans JP', sans-serif;
}

html, body{
	width:100% !important;
	height:100% !important;
	margin: 0px;
	font-size: 14px;
	padding:none;
	color:#CB87AF;
	color:var(--colorGlobal);
	font-family: 'Noto Sans JP', sans-serif;
}

button{
	background: #651142;
	background: var(--backgroundDarkerGlobal);
	font-weight: bold;
	border:0px;
	border-radius: 4px;
	color: #872D62;
	color: var(--backgroundGlobal);
	transition: background 1s;
}

button:hover{
	background: #A95486;
	background: var(--backgroundLigherGlobal);
}

::-webkit-input-placeholder{
	color:#CB87AF;
	color:var(--colorGlobal);
}

:-ms-input-placeholder{
	color:#CB87AF;
	color:var(--colorGlobal);
}

::-ms-input-placeholder{
	color:#CB87AF;
	color:var(--colorGlobal);
}

::placeholder{
	color:#CB87AF;
	color:var(--colorGlobal);
}

.globalWrapper{
	width:100vw;
	height:100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	text-align: center;

	background: #872D62;

	background: var(--backgroundGlobal);
}

a,a:visited{
	color:#651142;
	color:var(--backgroundDarkerGlobal);
	text-decoration: none;
}

a:hover{
	text-decoration: underline;
}

.globalLeftNav,.globalRightNav{
	position: fixed;
	width:5vw;
	height:50vh;
	font-size:4rem;
	transition: background 0.5s;
}

.globalLoadingMessage{
	font-weight: bold;
	font-size: 5rem;
}


.globalLeftNav:hover,.globalRightNav:hover{ background: #A95486; background: var(--backgroundLigherGlobal); }
.globalLeftNav:disabled,.globalRightNav:disabled{ display: none; }
.globalLeftNav{	left:0px; }
.globalRightNav{ right:0px; }

@media only screen and (max-width:800px){
	.globalLeftNav,.globalRightNav{
		font-size:2rem;
	}
}
.mpWrapper{
	width:100%;
	height:100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.mpInputTitle{
	width:75vw;
	height:10vh;
	font-size:5rem;
	text-align: center;
	font-weight: bold;
	border:none;
	background: var(--backgroundLigherGlobal);
	margin:0.5vh 1vw;
}

.mpSaveLink{

}

.mpInputStart, .mpInputWin, .mpInputLoss, .mpInputWinScore{
	width:90%;
	height:90%;
	margin:0.5vh 1vw;
	border: none;
	text-align: center;
	font-size: 2rem;
	font-weight: bold;
	background: none;
}

.mpQuizOption{
	margin:1vh auto;
	width:50vw;
	height:6vh;
	display: flex;
	justify-content: center;
	align-items: center;
	background: var(--backgroundLigherGlobal);
}

.mpQuestionInput{
	width:50vw;
	height:3vh;
	font-size:2rem;
	border:0px;
	border-radius: 4px;
}

.mpQuestionTitle{
	font-size: 3rem;
	font-weight: bold;
	margin:1vh auto;
}

.mpQuestionNavigationPrevious, .mpQuestionNavigationNew,.mpQuestionNavigationNext{
	margin:1vh 0.5vw;

	width:10vw;
	height:3vh;
}

.mpQuestionNavigationPrevious:disabled, .mpQuestionNavigationNew:disabled,.mpQuestionNavigationNext:disabled{
	background: var(--backgroundDisabledGlobal);
}

.mpQuestion, .mpQuestionCorrect{
	margin:0.1vw;
	width:50vw;
	height:3vh;
	border:0px;
	border-radius: 4px;
}

.mpQuestionCorrect{
	background: var(--backgroundCorrect);
}

.mpCorrect{
	border:0px;
	width:10vw;
	height:4vh;
	margin:0vh 0.5vw;
	transition: background 1s;
}

.mpSubmitQuizMessage{
	font-size:5rem;
	font-weight: bold;
}

.mpSubmitQuizButton{
	width:50vw;
	height:5vh;
	margin:1vh 1vh;
	font-size:2rem;
}

.mpAnswerSpan{
 	margin:0.25vh 0vw;
}

.submitLink{
	font-size:5rem;
	font-weight: bold;
}

@media only screen and (max-width:1000px){
	.mpInputTitle{
		font-size:3rem;
	}

	.mpInputStart, .mpInputWin, .mpInputLoss, .mpInputWinScore{
		font-size:1.5rem;
	}

	.mpWrapper,.mpQuestionNavigation{
		width:80vw;
	}

	.mpQuestionNavigationPrevious, .mpQuestionNavigationNew,.mpQuestionNavigationNext{
		width:25vw;
	}

	.mpSubmitQuizMessage{
		font-size:2.5rem;
	}

	.submitLink{
		font-size:2.5rem;
	}

	.mpSubmitQuizButton{
		width:70vw;
		height:5vh;
		margin:1vh 1vh;
		font-size:2rem;
	}
}

@media only screen and (max-width:400px){
	.mpInputTitle{
		font-size:1.5rem;
	}

	.mpInputStart, .mpInputWin, .mpInputLoss, .mpInputWinScore{
		font-size:0.8rem;
	}

	.mpQuestionInput{
		width:80vw;
		font-size:1rem;
	}

	.mpQuestionNavigationPrevious, .mpQuestionNavigationNew,.mpQuestionNavigationNext{
		width:25vw;
		font-size: 0.8rem;
	}

	.mpQuestion{
		width:60vw;
	}

	.submitLink{
		font-size:1rem;
	}

	.mpCorrect{
		width:15vw;
		font-size: 0.8rem;
	}

	.mpSubmitQuizMessage{
		font-size:1.25rem;
	}

	.mpSubmitQuizButton{
		width:70vw;
		font-size:1rem;
	}
}
.qTitle{
	font-size: 8rem;
	font-weight: bold;
	margin:5vh auto;
}

.qStartMessage, .qEndMessage{
	font-size:300%;
	font-weight: bold;
	margin:5vh auto;
	width:80vw;
}

.qQuestionCount{
	font-size:2rem;
}

.qStartButton{
	width:50vw;
	height:10vh;
	font-size: 3rem;
	font-weight: bold;
	background: var(--backgroundDarkerGlobal);
	color:var(--backgroundLigherGlobal);
	border:none;
}

.qQuestion{
	font-size:2rem;
	font-weight: bold;
}

.qAnswerButton{
	width:50vw;
	min-height:5vh;
	font-size:150%;
	transition: background 1s, color 1s;
}

.qAnswerButton:hover{
	background: var(--backgroundLigherGlobal);
	color:var(--backgroundDarkerGlobal);
}

.qResultQuestion, .qResultAnswer{
	margin:1vh auto;
	min-height:3vh;
}

.qResultQuestion{
	font-weight: bold;
}

.qCorrect, .qWrong{
	width:50vw;
	min-height:5vh;
	margin:1vh auto;
	color:black;	
}

.qCorrect{
	background: var(--backgroundCorrect);
}

.qWrong{
	background: red;
}

.qSaveResult{
	margin:5vh auto;
	font-size:1.5rem;
	font-weight: bold;	
	width:50vw;
}

.qSaveInputWrapper{
	display: flex;
	justify-content: center;
	align-items: center;
}

.qSaveInput{
	font-size:2rem;
	width:20vw;
	height:4vh;
}

.qSaveButton{
	width:10vw;
	height:5vh;
	border:none;
	background: var(--backgroundDarkerGlobal);
	color:var(--backgroundLigherGlobal);
	font-weight: bold;
	font-size:1rem;
	transition: background 1s, color 1s;
	margin:0vh 1vw;
}

.qResultLink{
	width:50vw;
	word-wrap: break-word;
}

@media only screen and (max-width:1000px){
	.qTitle{
		font-size: 4rem;
	}

	.qStartMessage, .qEndMessage{
		width:80vw;
		font-size:2rem;
	}

	.qQuestionCount, .qAnswerButton, .qAnswerEnd, .qCorrect, .qWrong, .qSaveResult, .qResultLink{
		width:80vw;
	}

	.qSaveInput{
		width:50vw;
		border: 0px;
		border-radius: 4px;
	}

	.qSaveButton{
		width:20vw;
	}
}


@media only screen and (max-width:400px){
	.qTitle{
		font-size: 3rem;
	}

	.qStartMessage, .qEndMessage{
		width:80vw;
		font-size:1rem;
	}

	.qQuestionCount{
		font-size:1rem;
	}

	.qQuestionCount, .qAnswerButton, .qAnswerEnd, .qCorrect, .qWrong, .qSaveResult, .qResultLink{
		width:90vw;
	}

	.qStartButton{
		width:80vw;
	}

	.qAnswerButton{
		font-size:1rem;
	}

	.qResultQuestion,.qResultAnswer{
		font-size:0.8rem;
		word-wrap: break-word;
	}

	.qSaveInput{
		font-size:1rem;
		width:60vw;
	}

	.qSaveButton{
		font-size:0.8rem;
		width:25vw;
	}
}
.rResultName{
	font-weight: bold;
	font-size:2rem;
}


.sbTitle{
	font-size: 5rem;
	font-weight: bold;
}

.sbEntry{
	background: var(--backgroundLigherGlobal);
	border-radius: 5px;
	width:50vw;
	min-height:5vh;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	margin:0.5vh auto;
}

.sbEntryPos{
	font-size:150%;
	font-weight: bold;
}

.sbEntryName{
	font-size:120%;
}

.sbEntryScore{
	font-size:80%;
}

@media only screen and (max-width:1000px){
	.sbEntry{
		width:80vw;
	}

	.sbTitle{
		font-size: 3rem;
	}

}


@media only screen and (max-width:400px){
	.sbEntry{
		width:90vw;
	}

	.sbTitle{
		font-size: 1.8rem;
	}

}
